<template>
  <div class="container mx-auto py-8 space-y-8">
    <!-- 主要功能区 -->
    <div
      class="bg-white rounded-xl shadow-sm border border-slate-200 p-6 md:p-8"
    >
      <!-- 操作模式切换 -->
      <div class="flex space-x-4 mb-6">
        <button
          @click="switchToTextMode"
          :class="[
            'px-4 py-2 rounded-lg font-medium flex items-center',
            isTextMode
              ? 'bg-primary-50 text-primary-700'
              : 'text-slate-600 hover:bg-slate-50',
          ]"
        >
          <img
            src="https://unpkg.com/lucide-static@latest/icons/type.svg"
            alt="文本"
            class="w-4 h-4 mr-2"
          />
          {{ t('modes.text') }}
        </button>
        <button
          @click="switchToFileMode"
          :class="[
            'px-4 py-2 rounded-lg font-medium flex items-center',
            !isTextMode
              ? 'bg-primary-50 text-primary-700'
              : 'text-slate-600 hover:bg-slate-50',
          ]"
        >
          <img
            src="https://unpkg.com/lucide-static@latest/icons/file.svg"
            alt="文件"
            class="w-4 h-4 mr-2"
          />
          {{ t('modes.file') }}
        </button>
      </div>

      <!-- 文本转换模式 -->
      <div v-show="isTextMode" class="space-y-6">
        <!-- 输入区域 -->
        <div>
          <div class="flex justify-between items-center mb-2">
            <label class="text-sm font-medium text-slate-700">{{ t('text_mode.input.label') }}</label>
            <div class="flex space-x-3">
              <button
                @click="pasteToInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/clipboard.svg"
                  alt="粘贴"
                  class="w-4 h-4 mr-1"
                />
                {{ t('text_mode.buttons.paste') }}
              </button>
              <button
                @click="copyInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  alt="复制"
                  class="w-4 h-4 mr-1"
                />
                {{ t('text_mode.buttons.copy') }}
              </button>
              <button
                @click="clearInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/x.svg"
                  alt="清空"
                  class="w-4 h-4 mr-1"
                />
                {{ t('text_mode.buttons.clear') }}
              </button>
            </div>
          </div>
          <textarea
            v-model="inputText"
            rows="6"
            class="w-full rounded-lg border border-slate-300 focus:border-primary-500 focus:ring-2 focus:ring-primary-500/20 resize-y p-3 text-slate-800 font-mono text-sm"
            :placeholder="t('text_mode.input.placeholder')"
          ></textarea>
        </div>

        <!-- 转换按钮组 -->
        <div class="flex justify-center space-x-4">
          <button
            @click="encode"
            class="px-6 py-2.5 bg-gradient-to-r from-primary-600 to-primary-700 hover:from-primary-700 hover:to-primary-800 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all flex items-center"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/arrow-down.svg"
              alt="编码"
              class="w-5 h-5 mr-2"
            />
            {{ t('text_mode.buttons.encode') }}
          </button>
          <button
            @click="decode"
            class="px-6 py-2.5 bg-slate-600 hover:bg-slate-700 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all flex items-center"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/arrow-down.svg"
              alt="解码"
              class="w-5 h-5 mr-2"
            />
            {{ t('text_mode.buttons.decode') }}
          </button>
        </div>

        <!-- 输出区域 -->
        <div>
          <div class="flex justify-between items-center mb-2">
            <label class="text-sm font-medium text-slate-700">{{ t('text_mode.output.label') }}</label>
            <div class="flex space-x-3">
              <button
                @click="copyOutput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  alt="复制"
                  class="w-4 h-4 mr-1"
                />
                {{ t('text_mode.buttons.copy') }}
              </button>
            </div>
          </div>
          <textarea
            v-model="outputText"
            rows="6"
            class="w-full rounded-lg border border-slate-300 bg-slate-50/50 p-3 text-slate-800 font-mono text-sm"
            readonly
          ></textarea>
        </div>
      </div>

      <!-- 文件转换模式 -->
      <div v-show="!isTextMode" class="space-y-6">
        <div
          class="border-2 border-dashed border-slate-300 rounded-lg p-8 text-center hover:border-primary-400 transition-colors"
          :class="{ 'border-primary-400': isDragging }"
          @dragover.prevent="isDragging = true"
          @dragleave="isDragging = false"
          @drop.prevent="handleFileDrop"
        >
          <div class="flex flex-col items-center">
            <img
              src="https://unpkg.com/lucide-static@latest/icons/upload.svg"
              alt="上传"
              class="w-12 h-12 text-slate-400 mb-4"
            />
            <p class="text-slate-600 mb-4">{{ t('file_mode.drop_zone.title') }}</p>
            <label
              class="px-6 py-2.5 bg-gradient-to-r from-primary-600 to-primary-700 hover:from-primary-700 hover:to-primary-800 text-white font-medium rounded-lg shadow-md hover:shadow-lg cursor-pointer transition-all"
            >
              {{ t('file_mode.drop_zone.select_button') }}
              <input type="file" class="hidden" @change="handleFileSelect" />
            </label>
          </div>
        </div>

        <!-- 文件信息和结果 -->
        <div v-if="selectedFile" class="space-y-6">
          <div class="bg-slate-50 rounded-lg p-4 mb-4">
            <div class="flex items-center justify-between mb-2">
              <div class="flex items-center">
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/file.svg"
                  alt="文件"
                  class="w-5 h-5 text-slate-500 mr-2"
                />
                <span class="text-slate-700 font-medium">{{
                  selectedFile.name
                }}</span>
              </div>
              <button
                @click="removeFile"
                class="text-slate-500 hover:text-slate-700"
                :title="t('file_mode.file_info.remove')"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/x.svg"
                  alt="移除"
                  class="w-4 h-4"
                />
              </button>
            </div>
            <div class="text-sm text-slate-500">{{ formattedFileSize }}</div>
          </div>

          <div class="space-y-4">
            <div class="flex items-center justify-between">
              <span class="text-sm font-medium text-slate-700">{{ t('file_mode.output.label') }}</span>
              <div class="flex space-x-3">
                <button
                  @click="copyFileOutput"
                  class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                    alt="复制"
                    class="w-4 h-4 mr-1"
                  />
                  {{ t('text_mode.buttons.copy') }}
                </button>
              </div>
            </div>
            <textarea
              v-model="fileOutputText"
              rows="6"
              class="w-full rounded-lg border border-slate-300 bg-slate-50/50 p-3 text-slate-800 font-mono text-sm"
              readonly
            ></textarea>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { copyToClipboard, clipboardText } from "~/utils/clipboard";
import CryptoJS from "crypto-js";
import { ref, computed } from "vue";

const { t } = useI18n({
  useScope: "local",
});

// 模式切换状态
const isTextMode = ref(true);

// 文本模式的数据
const inputText = ref("");
const outputText = ref("");

// 文件模式的状态
const selectedFile = ref(null);
const fileOutputText = ref("");
const isDragging = ref(false);

// 计算属性
const formattedFileSize = computed(() => {
  if (!selectedFile.value) return "";
  return formatFileSize(selectedFile.value.size);
});

// 切换模式的方法
const switchToTextMode = () => {
  isTextMode.value = true;
};

const switchToFileMode = () => {
  isTextMode.value = false;
};

/**
 * 使用 CryptoJS 将文本编码为 Base64
 * @param {string} text - 需要编码的文本
 * @returns {string} - 编码后的 Base64 字符串
 */
const encodeBase64 = (text) => {
  try {
    // 使用 CryptoJS 进行 Base64 编码
    return CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(text));
  } catch (error) {
    console.error("编码错误:", error);
    return t('errors.encoding');
  }
};

/**
 * 使用 CryptoJS 将 Base64 解码为文本
 * @param {string} base64 - 需要解码的 Base64 字符串
 * @returns {string} - 解码后的文本
 */
const decodeBase64 = (base64) => {
  try {
    // 使用 CryptoJS 进行 Base64 解码
    return CryptoJS.enc.Utf8.stringify(CryptoJS.enc.Base64.parse(base64));
  } catch (error) {
    console.error("解码错误:", error);
    return t('errors.decoding');
  }
};

// 文本模式的功能函数
/**
 * 编码操作 - 将输入文本转换为 Base64
 */
const encode = () => {
  outputText.value = encodeBase64(inputText.value);
};

/**
 * 解码操作 - 将 Base64 转换为文本
 */
const decode = () => {
  outputText.value = decodeBase64(inputText.value);
};

/**
 * 将剪贴板内容粘贴到输入框
 */
const pasteToInput = async () => {
  try {
    inputText.value = await clipboardText();
  } catch (error) {
    console.error("剪贴板粘贴错误:", error);
  }
};

/**
 * 复制输入框内容到剪贴板
 */
const copyInput = () => {
  copyToClipboard(inputText.value);
};

/**
 * 清空输入框
 */
const clearInput = () => {
  inputText.value = "";
};

/**
 * 复制输出结果到剪贴板
 */
const copyOutput = () => {
  copyToClipboard(outputText.value);
};

/**
 * 使用 CryptoJS 将文件转换为 Base64
 * @param {File} file - 需要转换的文件对象
 * @returns {Promise<string>} - 转换后的 Base64 字符串
 */
const fileToBase64 = (file) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    reader.onload = () => {
      try {
        // 将 ArrayBuffer 转换为 WordArray
        const wordArray = CryptoJS.lib.WordArray.create(reader.result);
        // 转换为 Base64
        const base64 = CryptoJS.enc.Base64.stringify(wordArray);
        resolve(base64);
      } catch (error) {
        reject(error);
      }
    };
    reader.onerror = (error) => reject(error);
  });
};

/**
 * 格式化文件大小
 * @param {number} bytes - 文件字节数
 * @returns {string} - 格式化后的文件大小（如：1.5 MB）
 */
const formatFileSize = (bytes) => {
  if (bytes === 0) return "0 Bytes";
  const k = 1024;
  const sizes = ["Bytes", "KB", "MB", "GB", "TB"];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + " " + sizes[i];
};

// 文件处理相关函数
/**
 * 处理文件选择
 * @param {Event} event - 文件选择事件
 */
const handleFileSelect = async (event) => {
  if (event.target.files && event.target.files[0]) {
    selectedFile.value = event.target.files[0];
    await processSelectedFile();
  }
};

/**
 * 处理文件拖放
 * @param {DragEvent} event - 拖放事件
 */
const handleFileDrop = async (event) => {
  isDragging.value = false;
  if (event.dataTransfer.files && event.dataTransfer.files[0]) {
    selectedFile.value = event.dataTransfer.files[0];
    await processSelectedFile();
  }
};

/**
 * 处理选中的文件并转换为 Base64
 */
const processSelectedFile = async () => {
  try {
    fileOutputText.value = await fileToBase64(selectedFile.value);
  } catch (error) {
    console.error("文件处理错误:", error);
    fileOutputText.value = t('errors.file_processing');
  }
};

/**
 * 移除已选择的文件
 */
const removeFile = () => {
  selectedFile.value = null;
  fileOutputText.value = "";
};

/**
 * 复制文件转换结果到剪贴板
 */
const copyFileOutput = () => {
  copyToClipboard(fileOutputText.value);
};
</script>

